* {
  box-sizing: border-box;
}
body {
  padding: 0;
  margin: 0;
  font-family: 'Space Grotesk', sans-serif;
  font-weight: 0;
}
.canvas {
  display: flex;
  flex-direction: row;
}
.left {
  width: 700px;
  height: 100vh;
}

form {
  margin: 0;
  width: 50%;
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding-left: 120px;
}

img {
  display: block;
  margin: 0;
  padding: 0;
  max-height: 100%;
}

.card {
  width: 380px;
}
.back {
  position: absolute;
  top: 400px;
  left: 230px;
}
.front {
  position: absolute;
  top: 150px;
  left: 160px;
}
.shadow {
  border-radius: 12px;
  box-shadow: 0 0 20px rgb(0 0 0 / 0.6);
}
p {
  margin: 0;
}
.cvc {
  width: 300px;
  text-align: right;
  left: 40px;
  top: 92px;
}
.card-text {
  position: absolute;
  color: white;
}
.name {
  left: 25px;
  top: 165px;
}

.exp {
  left: 300px;
  top: 165px;
}
.card-number {
  font-size: 24px;
  left: 25px;
  top: 115px;
}
.logo {
  width: 80px;
  position: absolute;
  left: 30px;
  top: 20px;
}
input {
  font-family: 'Space Grotesk', sans-serif;
  width: 380px;
  font-size: 18px;
  border-radius: 5px;
  height: 50px;
  outline: none;
  border: 1px solid hsl(270, 3%, 87%);
  padding: 0 10px;
}
input:focus {
  border-color: hsl(278, 94%, 30%);
}
::placeholder {
  color: hsl(270, 3%, 87%);
}
label {
  margin-bottom: 5px;
  font-size: 12px;
}
.top {
  width: 380px;
  margin-bottom: 5px;
}
.cvc-label {
  margin-left: 74px;
}
.date {
  margin-right: 10px;
  width: 80px;
}
.cvc-input {
  flex-grow: 1;
  width: calc(100% - 180px);
}
.bot {
  width: 380px;
  display: flex;
}
.btn {
  user-select: none;
  cursor: pointer;
  padding-top: 13px;
  border-radius: 8px;
  text-align: center;
  width: 380px;
  color: white;
  height: 50px;
  background-color: hsl(278, 68%, 11%);
}
.wrong {
  height: 30px;
  margin-top: 5px;
  font-size: 11px;
  color: hsl(0, 100%, 66%);
}
.wrong-block {
  display: flex;
}

.yw {
  width: 182px;
}
.complete {
  margin-left: 145px;
  width: 80px;
  height: 80px;
  margin-bottom: 30px;
}

.thank {
  margin-bottom: 10px;
  color: hsl(278, 68%, 11%);
  font-size: 30px;
  width: 380px;
  text-align: center;
}
.details {
  color: hsl(279, 6%, 55%);
  width: 380px;
  text-align: center;
  margin-bottom: 40px;
}
